  color: #23527c;
  text-decoration: underline;
}

.btn {
  color: #333;
  background-color: #e6e6e6;
  border-color: #adadad;
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
}

.btn:hover {
  color: #333;
  background-color: #e6e6e6;
  border-color: #adadad;
  text-decoration: none;
}

input[type="text"], input[type="email"], input[type="password"] {
  padding-left: 10px;
  height: 38px;
  border: 1px solid #ddd;
  font-size: 16px;
  -webkit-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

input.input-form.ng-touched.ng-valid {
  border: 1px solid #1CA347;
}

input.input-form.ng-touched.ng-invalid {
  border: 1px solid #d9534f;
}

input:hover, input:focus {
  outline: none;
  box-shadow: none;
  border-color: #ccc;
}

.btn-service-whale {
  outline: none;
  color: #fff;
  font-size: 14px;
  height: auto;
  font-weight: normal;
  padding: 14px 10px;
  text-transform: uppercase;
  border-color: #59B2E6;
}

.btn-service-whale:hover, .btn-service-whale:focus {
  color: #fff;
